.blue { color: #0074D9; }
.bg-blue { background-color: #0074D9; }

:root {
    .alegreya-400 {
        font-family: "Alegreya", serif;
        font-optical-sizing: auto;
        font-weight: 400;
        font-style: normal;
      }
    .alegreya-700 {
        font-family: "Alegreya", serif;
        font-optical-sizing: auto;
        font-weight: 700;
        font-style: normal;
      }
    .alegreya-sans-sc-light {
        font-family: "Alegreya Sans SC", sans-serif;
        font-weight: 300;
        font-style: normal;
      }
    .zen-maru-gothic-700 {
        font-family: "Zen Maru Gothic", serif;
        font-weight: 700;
        font-style: normal;
      }

    h1, h2, h3, h4, h5, h6 {
        font-family: "Alegreya", serif;
        font-optical-sizing: auto;
        font-weight: 700;
        font-style: normal;
      }
}

html {
    background-color: #52013f;
}
main {
    font-family: "Alegreya", serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
}

svg {
width: 1em;
height: 1em;
vertical-align: middle;
padding-bottom: 0.25em;
}

img {
border-style: solid;
border-width: 1px;
border-color: gainsboro;
border-radius: 0.3rem;
}




