github.com style (c) Vasily Polovnyov <vast@whiteants.net>
*/

.hljs {
    display: block;
    overflow-x: auto;
    padding: 0.5em;
    color: #111;
    color: rgba(0,0,0,.7);
    background-color: #fff;
    background-color: rgba(255,255,255,.2);
    }

    .hljs-comment,
    .hljs-quote {
    color: #998;
    font-style: italic;
    }

    .hljs-keyword,
    .hljs-selector-tag,
    .hljs-subst {
    color: #333;
    font-weight: bold;
    }

    .hljs-number,
    .hljs-literal,
    .hljs-variable,
    .hljs-template-variable,
    .hljs-tag .hljs-attr {
    color: #006C71;
    }

    .hljs-string,
    .hljs-doctag {
    color: #C64774;
    }

    .hljs-title,
    .hljs-section,
    .hljs-selector-id {
    color: #900;
    font-weight: bold;
    }

    .hljs-subst {
    font-weight: normal;
    }

    .hljs-type,
    .hljs-class .hljs-title {
    color: #458;
    font-weight: bold;
    }

    .hljs-tag,
    .hljs-name,
    .hljs-attribute {
    color: #00449E;
    font-weight: normal;
    }

    .hljs-regexp,
    .hljs-link {
    color: #009926;
    }

    .hljs-symbol,
    .hljs-bullet {
    color: #990073;
    }

    .hljs-built_in,
    .hljs-builtin-name {
    color: #0086b3;
    }

    .hljs-meta {
    color: #999;
    font-weight: bold;
    }

    .hljs-deletion {
    background: #fdd;
    }

    .hljs-addition {
    background: #dfd;
    }

    .hljs-emphasis {
    font-style: italic;
    }

    .hljs-strong {
    font-weight: bold;
    }
